.tradeoff-slider {
  background: #d3d3d3;
  outline: black;
  opacity: 0.7;
  -webkit-transition: .2s;
  -webkit-appearance: none;
  transition: opacity .2s;
  position: absolute;
  top:2cd 50px;
}

.buttons{
  position: absolute;
  top: 1300px;
  left: 300px;
}

.bartleby{
  position : absolute;
  top : 300px;
  left : 400px;
}

.nextQuestion{
  position : absolute;
  top : 400px;
  left: 400px;
}

.b{
  position: absolute;
  transform: scaleY(-1);
  top: 400px;
  left: 0px;
}

.wrap{
position : relative;
}
.c{
position: absolute;
top:450px;
left:0px;
}
.e{
position: absolute;
top:450px;
left: 400px;
}
.d{
  transform: rotate(180deg);
}
#holder {
position:relative
}

.tradeoff-slider::-moz-range-thumb {
    width: 10px;  
    height: 10px;
    background: #4CAF50;
    cursor: pointer;
} 
