.boxed-threshold {
    border: 1px solid black;
    display: inline-block;
    padding: 10px;
    max-width: 70%;
  } 
  
  .thresh-top-slider {
    -webkit-appearance: none;
    height: 5px;
    outline: none;
    background: none;
    opacity: 1;
    -webkit-transition: .2s;
    transition: opacity .2s;
    position: relative;
    top:-300px;
  }
  
  .thresh-top-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 2px;
    background: black;
    cursor: pointer;
    opacity: 1;
    height: 75px;
  }
  
  .thresh-bottom-slider{
    -webkit-appearance: none;
    height: 10px;
    outline: none;
    background: none;
    opacity: 1;
    -webkit-transition: .2s;
    transition: opacity .2s;
    position: relative;
    top: -220px;
  
  }
  .thresh-bottom-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 2px;
    background:black;
    cursor: pointer;
    opacity: 1;
    height: 75px;
  }